import * as React from 'react';
import { useNavigate } from 'react-router-dom';

import Staff from '@miq/staffjs';

import { Cart } from './utils';
import { Button, Icons } from '@miq/componentjs';

import { TCustomer, CustomerForm, CustomerCard, Customer } from '../customer';
import { useDocumentTitle } from '@miq/hookjs';

export default function CartCreateView() {
  useDocumentTitle('Add cart - Sales');
  const navigate = useNavigate();
  const [customer, setCustomer] = React.useState<TCustomer>();

  const createCart = (customer: TCustomer) => {
    Cart({})
      .create({ customer: customer.slug })
      .then(({ data }) => {
        return navigate(`/staff/sales/carts/${data.slug}/`);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <Staff.View title="Ajouter une commande" back="/staff/sales/">
      <div className="d-flex justify-content-center my-3">
        <div className="w-md-65">
          <Staff.Section title="Client" text="Selectionez ou ajoutez un noubeau client" headerCN="mb-4">
            {customer && customer.slug ? (
              <div className="">
                <CustomerCard data={customer} />

                <div className="d-flex justify-content-between mt-4">
                  <Button className="btn btn-round" onClick={() => setCustomer(undefined)}>
                    <Icons.ArrowLeft />
                  </Button>

                  <Button
                    onClick={() => {
                      createCart(customer);
                    }}
                    className="btn btn-primary-3"
                  >{`Continuer »`}</Button>
                </div>
              </div>
            ) : (
              <CustomerForm
                fields={['first_name', 'last_name', 'phone', 'email']}
                onSubmit={({ e, form }) => {
                  e.preventDefault();
                  Customer()
                    .create(form.values)
                    .then(({ data }) => {
                      createCart(data);
                    })
                    .catch((err) => {
                      form.handleError(err);
                    });
                }}
                onSuccess={({ data }) => {}}
                onCustomerSelect={setCustomer}
              >
                <div className="d-flex justify-content-center my-4">
                  <Button type="submit" className="btn btn-md btn-primary fw-bold">
                    {`Continuer »`}
                  </Button>
                </div>
              </CustomerForm>
            )}
          </Staff.Section>
        </div>
      </div>
    </Staff.View>
  );
}
